import React, { useEffect , memo} from 'react';
import {Table} from 'react-bootstrap';
import {connect} from 'react-redux';

// import Table : export default 
// import {Table} : Table / 

function Cart(props){
    return (
        <div>
            <Table striped>
                <thead>
                    <tr>
                    <th>#</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        props.state.map((a,i)=>{
                            return( 
                            <tr key={i}>
                                <td>{ a.id }</td>
                                <td>{ a.name }</td>
                                <td>{ a.quan }</td>
                                <td>
                                    <button onClick={()=>{ props.dispatch({type : 'addQuan', payload: a.id }) }}>+</button>
                                    <button onClick={()=>{ props.dispatch({type : 'minusQuan', payload: a.id }) }}>-</button>
                                </td>
                            </tr>
                            )
                        })
                    }         
                </tbody>
                </Table>
                {   props.bStatus &&
                    <div className="my-alert2">
                        <p>   20%</p>
                        <button onClick={()=>{props.dispatch({type:'closeAlert'})}}></button>
                    </div>
                }
            <Parent name="cha" age="27"/>
        </div>
    )
}

function stateporops(state){
    return {
        state : state.reducer,
        bStatus : state.reducer2
    }
}

function Parent(props){
    return(
        <div>
            <Child1 name={props.name}/>
            <Child2 age={props.age}/>
        </div>
    )
}

function Child1(props){
    useEffect(()=>{console.log('1')});
    return <div>1111</div>
}

// memo :  props VS  props    
let Child2 = memo(function(props){
    useEffect(()=>{console.log('2')});
    return <div>2222</div>
});

export default connect(stateporops)(Cart)
// export default Cart;